window.onload = () => {
	const c = document.getElementById("img-carousel");
	if (c) {
		const imgs = c.getElementsByClassName("project-img");
		if (imgs && imgs.length > 1) {
			for (let i = 1; i < imgs.length; ++i) {
				imgs[i].style.display = "none";
			}
			start(imgs);
		}
	}
}

function start(imgs) {
	let index = 0;
	setInterval(() => {
		imgs[index].style.display = "none";
		index = (index + 1) % imgs.length;
		imgs[index].style.display = "block";
	}, 5000);
}